import React from 'react';

import {bem} from 'shared/bem';

import './select-modal.scss';

const b = bem('select-modal');

type Props = {
  onSelect: (value: string) => void;
  onClose: () => void;
  label: string;
  value: string;
};

export const SelectModalItem = ({label, onSelect, onClose, value}: Props) => {
  const handleSelect = () => {
    onSelect(value);
    onClose();
  };

  return (
    <div className={b('option')} onClick={handleSelect}>
      {label}
    </div>
  );
};
