import React, {Suspense} from 'react';
import {Routes, Route, Navigate} from 'react-router-dom';

import {Main} from 'pages/main';
import {Agreement} from 'pages/agreement';
import {Auth} from 'pages/auth';
import {Market} from 'pages/market';
import {Personal} from 'pages/personal';
import {Exchange} from 'pages/exchange';
import {Spinner} from 'shared/ui';

import {App} from '../App';

export const Routing = () => {
  return (
    <Suspense fallback={<Spinner style={{position: 'absolute', top: '50%', left: '50%'}} />}>
      <Routes>
        <Route path={'/'} element={<App />}>
          <Route index element={<Main />} />
          <Route path={'agreement'} element={<Agreement />} />
          <Route path={'market'} element={<Market />} />
          <Route path={'deposit'} element={<Personal />} />
          <Route path={'personal'} element={<Personal />} />
          <Route path={'withdraw'} element={<Personal />} />
          <Route path={'support'} element={<Personal />} />
          <Route path={'settings'} element={<Personal />} />
          <Route path={'verification'} element={<Personal />} />
          <Route path={'exchange'} element={<Exchange />} />
          <Route path={'login'} element={<Auth />} />
          <Route path={'signup'} element={<Auth />} />
        </Route>
        <Route path={'*'} element={<Navigate replace to={'/'} />} />
      </Routes>
    </Suspense>
  );
};
