import React from 'react';

import {bem} from 'shared/bem';
import * as Tables from 'shared/ui/tables';
import {useUser} from 'services/user';

import {DepositTableItem} from './deposits-table-item';
import {TableContentBlank} from '../table-content-blank';

import './deposits-table.scss';

const b = bem('deposits-table');

export const DepositsTable = () => {
  const {user} = useUser();

  const deposits = user?.deposits || [];

  return (
    <div className={b()}>
      <Tables.Table>
        <Tables.Caption>Deposits requests</Tables.Caption>
        {deposits.length ? (
          <>
            <Tables.Head>
              <Tables.Row>
                <Tables.HeadCell>Coin</Tables.HeadCell>
                <Tables.HeadCell>Date</Tables.HeadCell>
                <Tables.HeadCell>Amount</Tables.HeadCell>
                <Tables.HeadCell>Network</Tables.HeadCell>
                <Tables.HeadCell>Address</Tables.HeadCell>
                <Tables.HeadCell>Status</Tables.HeadCell>
              </Tables.Row>
            </Tables.Head>
            <Tables.Body>
              {deposits.map((item, index) => (
                <DepositTableItem key={`${item.date}-${index}`} deposit={item} />
              ))}
            </Tables.Body>
          </>
        ) : (
          <Tables.Body>
            <Tables.Row>
              <Tables.Cell>
                <TableContentBlank message={'No deposit requests'} />
              </Tables.Cell>
            </Tables.Row>
          </Tables.Body>
        )}
      </Tables.Table>
    </div>
  );
};
