import React from 'react';

import {bem} from 'shared/bem';
import * as Tables from 'shared/ui/tables';

import './withdraw-table.scss';
import {TableContentBlank} from '../table-content-blank';

const b = bem('deposits-table');

export const WithdrawTable = () => {
  const withdraws = [];

  return (
    <div className={b()}>
      <Tables.Table>
        <Tables.Caption>Withdraw requests</Tables.Caption>
        {withdraws.length ? (
          <>
            <Tables.Head>
              <Tables.Row>
                <Tables.HeadCell>Coin</Tables.HeadCell>
                <Tables.HeadCell>Date</Tables.HeadCell>
                <Tables.HeadCell>Amount</Tables.HeadCell>
                <Tables.HeadCell>Network</Tables.HeadCell>
                <Tables.HeadCell>Address</Tables.HeadCell>
                <Tables.HeadCell>Status</Tables.HeadCell>
              </Tables.Row>
            </Tables.Head>
            <Tables.Body></Tables.Body>
          </>
        ) : (
          <Tables.Body>
            <Tables.Row>
              <Tables.Cell>
                <TableContentBlank message={'No withdraw requests'} />
              </Tables.Cell>
            </Tables.Row>
          </Tables.Body>
        )}
      </Tables.Table>
    </div>
  );
};
