import React from 'react';

import {Row, Cell} from 'shared/ui/tables';

type Props = {
  coin: string;
  value: string;
};

export function BalanceTableItem({coin, value}: Props) {
  return (
    <Row>
      <Cell>{coin}</Cell>
      <Cell>{value}</Cell>
      <Cell>{value}</Cell>
      <Cell>${value}</Cell>
    </Row>
  );
}
