import React from 'react';
import {Link, useNavigate} from 'react-router-dom';

import {useAuth} from 'services/auth';
import {bem} from 'shared/bem';

import './extend-area.scss';

const b = bem('extend-area');

export const ExtendArea = () => {
  const {logOut} = useAuth();
  const navigate = useNavigate();

  const handleLogOut = async () => {
    try {
      await logOut();
    } finally {
      navigate('/');
    }
  };

  return (
    <ul className={b()}>
      <li className={b('header')}>mock*****@shbiso.com</li>
      <p className={b('line')} />
      <li>
        <Link to={'personal'} className={b('link')}>
          Overview
        </Link>
      </li>
      <li>
        <Link to={'support'} className={b('link')}>
          Support
        </Link>
      </li>
      <li>
        <Link to={'personal'} className={b('link')}>
          Verification
        </Link>
      </li>
      <p className={b('line')} />
      <li onClick={handleLogOut}>Logout</li>
    </ul>
  );
};
