import React, {useEffect} from 'react';
import {useLocation} from 'react-router-dom';

import {useUser} from 'services/user';
import {bem} from 'shared/bem';
import {Deposit, Sidebar, Support, Dashboard, Settings, Withdraw} from 'components/personal';
import {Verification} from 'components/personal/verification';

import './personal.scss';

const b = bem('personal');

export const Personal = () => {
  const {pathname} = useLocation();
  const {getUser, getDictionary} = useUser();

  useEffect(() => {
    getUser();
    getDictionary();
  }, []);

  const getScreen = () => {
    switch (pathname) {
      case '/personal':
        return <Dashboard />;
      case '/deposit':
        return <Deposit />;
      case '/withdraw':
        return <Withdraw />;
      case '/settings':
        return <Settings />;
      case '/support':
        return <Support />;
      case '/verification':
        return <Verification />;
      default:
        return <Dashboard />;
    }
  };

  return (
    <div className={b()}>
      <div className={b('container')}>
        <Sidebar />
        <div className={b('wrapper')}>{getScreen()}</div>
      </div>
    </div>
  );
};
