import React, {useEffect} from 'react';

import {Modal} from 'shared/ui/modal';
import {bem} from 'shared/bem';
import {HamburgerIcon} from 'shared/ui';
import {navItems} from 'components/navbar/consts';

import {useUser} from '../../services/user';
import {AuthButtonsGroup} from '../auth-buttons-group';
import './navbar-modal.scss';
import {useAuth} from '../../services/auth';
import {NavbarItem} from '../navbar/navbar-item';

const b = bem('navbar-modal');

type Props = {
  onClose: () => void;
};

export const NavbarModal = ({onClose}: Props) => {
  const {user, getUser} = useUser();
  const {isLoggedIn} = useAuth();
  console.log(user);

  useEffect(() => {
    getUser();
  }, []);

  return (
    <Modal onClose={onClose} className={b()} classNameContent={b('content')} side={true}>
      <div className={b('header')}>
        {!isLoggedIn && <AuthButtonsGroup className={b('btn')} onClose={onClose} />}
        <div onClick={onClose} className={b('hamburger')}>
          <HamburgerIcon />
        </div>
      </div>

      <div className={b('nav-list')}>
        {navItems.map((item) => (
          <NavbarItem key={item.link} item={item} className={b('nav-link')} onClose={onClose} />
        ))}
      </div>
    </Modal>
  );
};
